.drawing-options-panel {
  display: flex;
  align-items: flex-end; }
  @media screen and (max-width: 500px) {
    .drawing-options-panel {
      flex-wrap: wrap; } }
  .drawing-options-panel__radio-buttons {
    width: 120px;
    height: 50px;
    position: relative;
    margin-right: 10px; }
  .drawing-options-panel__eraser {
    cursor: pointer;
    z-index: 2;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 10px;
    opacity: 0; }
    .drawing-options-panel__eraser:checked ~ .drawing-options-panel__eraser-image {
      background: #7953ac;
      box-shadow: 1px 4px 13px 3px black; }
  .drawing-options-panel__brush {
    cursor: pointer;
    z-index: 2;
    width: 50px;
    height: 50px;
    position: absolute;
    opacity: 0; }
    .drawing-options-panel__brush:checked ~ .drawing-options-panel__brush-image {
      background: #7953ac;
      box-shadow: 1px 4px 13px 3px black; }
  .drawing-options-panel__eraser-image {
    width: 50px;
    height: 50px;
    font-family: 'Material Icons';
    font-size: 50px;
    background: white;
    border-radius: 4px;
    position: absolute;
    /* right: 50px; */
    right: 10px;
    box-shadow: 0px 3px 7px 0px black;
    color: black; }
  .drawing-options-panel__brush-image {
    width: 50px;
    height: 50px;
    font-family: 'Material Icons';
    font-size: 50px;
    background: white;
    border-radius: 4px;
    position: absolute;
    box-shadow: 0px 3px 7px 0px black;
    color: black; }
  .drawing-options-panel__opacity-input-container {
    width: 150px;
    display: flex;
    flex-direction: column;
    margin-right: 15px; }
  .drawing-options-panel__opacity-input-title {
    font-size: 20px;
    font-weight: bold;
    color: black; }
  .drawing-options-panel__opacity-input {
    width: 100%;
    height: 50px;
    padding: 5px 15px;
    font-size: 30px;
    outline: none;
    box-shadow: 0px 3px 7px 0px black;
    border-radius: 4px; }
  .drawing-options-panel__brush-size-input-container {
    width: 150px;
    display: flex;
    flex-direction: column; }
  .drawing-options-panel__brush-size-input-title {
    font-size: 20px;
    font-weight: bold;
    color: black; }
  .drawing-options-panel__brush-size-input {
    width: 100%;
    height: 50px;
    padding: 5px 15px;
    font-size: 30px;
    outline: none;
    box-shadow: 0px 3px 7px 0px black;
    border-radius: 4px; }
