*,
*:after,
*:before {
  margin: 0px;
  padding: 0px;
  border: 0px;
  box-sizing: border-box; }

html {
  font-family: Merriweather, Georgia, serif;
  font-size: 16px;
  color: rgba(31, 32, 65, 0.75);
  background: #f6f3f3; }

html,
body {
  min-width: 100%;
  display: flex;
  flex-direction: column; }
