.task {
  width: 100%;
  height: 100%; }
  .task__options-bar {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 20px; }
    @media screen and (max-width: 1000px) {
      .task__options-bar {
        flex-direction: column;
        align-items: flex-start; } }
  .task__dropdown-form {
    width: 450px; }
    @media screen and (max-width: 1000px) {
      .task__dropdown-form {
        order: 2; } }
    @media screen and (max-width: 490px) {
      .task__dropdown-form {
        width: 100%; } }
  @media screen and (max-width: 1000px) {
    .task__drawing-options-panel {
      order: 1;
      margin-bottom: 20px; } }
  .task__canvases {
    width: 100%;
    display: flex;
    position: relative; }
  .task__map-canvas, .task__result-canvas, .task__temp-canvas, .task__cursor-canvas {
    position: absolute; }
